import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  
  {
    path: '/',
    redirect: '/home',
    component: () => import('../views/HomeView.vue'),
    children: [
      {
        path: 'home',
        name:'home',
        components: {
        main: () => import('../views/content/HomeContent.vue'),
        side:()=>import('../views/HomeSide.vue')
        }
      },
      {
        path: 'quickStart',
        name:'quickStart',
        components: {
        main: () => import('../views/content/quickStartContent.vue'),
        side:()=>import('../views/HomeSide.vue')
        }
      },
      {
        path: 'cpnt/DyIcon',
        name: 'DyIcon',
        components: {
          main: () => import('../views/content/cpnt/DyIcon/MainView.vue'),
          side:()=>import('../views/content/cpnt/DyIcon/ShowView.vue')
        }
      },
      {
        path: 'cpnt/YftAvator',
        name: 'YftAvator',
        components: {
          main: () => import('../views/content/cpnt/YftAvator/MainView.vue'),
          side:()=>import('../views/content/cpnt/YftAvator/ShowView.vue')
        }
      },
      {
        path: 'cpnt/DyMessage',
        name: 'DyMessage',
        components: {
          main: () => import('../views/content/cpnt/DyMessage/MainView.vue'),
          side:()=>import('../views/content/cpnt/DyMessage/ShowView.vue')
        }
      },
      {
        path: 'cpnt/LuoSideNavgation',
        name: 'LuoSideNavgation',
        components: {
          main: () => import('../views/content/cpnt/LuoSideNavgation/MainView.vue'),
          side:()=>import('../views/content/cpnt/LuoSideNavgation/ShowView.vue')
        }
      },
      {
        path: 'cpnt/YftDropdown',
        name: 'YftDropdown',
        components: {
          main: () => import('../views/content/cpnt/YftDropdown/MainView.vue'),
          side:()=>import('../views/content/cpnt/YftDropdown/ShowView.vue')
        }
      },
      {
        path: 'cpnt/DyUploader',
        name: 'DyUploader',
        components: {
          main: () => import('../views/content/cpnt/DyUploader/MainView.vue'),
          side:()=>import('../views/content/cpnt/DyUploader/ShowView.vue')
        }
      },
      {
        path: 'cpnt/LuoDateTimePicker',
        name: 'LuoDateTimePicker',
        components: {
          main: () => import('../views/content/cpnt/LuoDateTimePicker/MainView.vue'),
          side:()=>import('../views/content/cpnt/LuoDateTimePicker/ShowView.vue')
        }
      },
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
